import React from 'react';
import Home from './Home'
import Login from './Login'
import Schools from './Schools'

function Body(props) {
  return (
    <div className="container">
      {props.active === 'home' && 
        <Home />
      }
      {props.active === 'schools' && 
        <Schools />
      }
      {props.active === 'login' && 
        <Login />
      }
      
    </div>
  );
}

export default Body;
